import React from 'react'
import { Row } from 'react-bootstrap';
import { NavLink, useRouteMatch, Route, Redirect, Switch } from 'react-router-dom';
import AuthLogs from './AuthLogs';
import ActivityLogs from './ActivityLogs';

const logRoutes = [
    {
        path: '/activity-logs',
        pathname: '/activity-logs',
        title: 'Activities',
        component: ActivityLogs
    },
    {
        path: '/auth-logs',
        pathname: '/auth-logs',
        title: 'Authentications',
        component: AuthLogs
    },
]

function Logs(props) {

    let { path, url } = useRouteMatch();

    const pathname1 = props.location.pathname.split('/')[2];


    React.useEffect(() => {
    }, [url]);

    const SettingsNav = logRoutes.map((instance, index) => {
        const className = pathname1 === instance.pathname ?
            `nav-item nav-link active nav-link2` :
            `nav-item nav-link nav-link2`
        return (
            <NavLink
                key={index}
                to={`${url}${instance.pathname}`}
                className={className}
            >
                <b>{instance.title}</b>
            </NavLink>)
    });

    const Routes = logRoutes.map((route, index) => {
        return <Route
            key={index}
            path={`${url}${route.pathname}`}
            name={route.name}
            component={propps => (
                <route.component {...propps} />
            )} />
    })

    return (
        <>
            <Row className="d-flex                                                                              justify-content-between pageHead" >
                <h4 className="m-b-20">Logs</h4>
                <div>

                </div>
            </Row>
            <nav className="nav nav-tabs" role="tablist">
                {SettingsNav}
            </nav>
            <div className="tab-content">
                <Switch>
                    {Routes}
                    <Redirect from='/logs' to='/logs/activity-logs' />
                </Switch>
                {/* <Redirect exact from="/settings" to='/settings/general-settings' /> */}
            </div>
        </>
    )
}

export default Logs;
