import React from 'react';
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Loader from '../../App/components/Reusables/Loader';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getAuthLogsSearchFilterObject } from '../../helpers/getSearchFilterObject';
import { getAuthLogs } from '../../api/userManagement/logs';
import { getStartEnd } from '../../helpers/getStartEnd'
import AuthLogsFilter from '../../App/components/SearchFilters/AuthLogsFilter';

const AuthLogs = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [logsArr, setLogsArr] = React.useState([]);
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();
        const requestJson = {
            "action": "get",
            "filter": "none",
            ...getAuthLogsSearchFilterObject(props),
            ...getStartEnd(props)
        };
        console.log(JSON.stringify(requestJson))
        getAuthLogs(requestJson, controller.signal)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setLogsArr(res.json.logs);
                    setInstanceCount(res.json.count);
                    setLoading(false);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });
        return () => controller.abort()
    }, []);



    const LogsList = logsArr.map((instance, index) => {
        return (
            <tr key={index}>
                <th scope="row">{instance.id}</th>
                <th scope="row">{instance.username}</th>

                <td>{instance.ip}</td>
                <td>{instance.action}</td>
                <td>{(new Date(instance.log_time)).toString()}</td>


            </tr>
        )
    })

    return (
        <Aux>
            <Row>
                <Col>
                    <AuthLogsFilter />
                    <Table striped responsive>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>User</th>
                                <th>IP Address</th>
                                <th>Action</th>
                                <th>Log Time</th>
                            </tr>
                        </thead>
                        <tbody>
                            {LogsList || ''}
                        </tbody>
                    </Table>
                    {isEmpty(logsArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                    {loading ? <Spinner /> : null}
                    <Paging instanceCount={instanceCount} />

                </Col>
            </Row>
        </Aux>
    );
}

export default AuthLogs;