import { baseRequest } from '../base'

const getAuthLogs = async (data, signal) => {
    try {
        return await baseRequest("api/v1/user/auth/logs", "POST", data, signal);
    } catch (e) {
        alert(e);
    }
};
const getActivityLogs = async (data, signal) => {
    try {
        return await baseRequest("api/v1/user/activities/logs", "POST", data, signal);
    } catch (e) {
        alert(e);
    }
};




export {
    getAuthLogs,
    getActivityLogs,
}