import React from 'react';
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Loader from '../../App/components/Reusables/Loader';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getAuthLogsSearchFilterObject, getActivityLogsFilter } from '../../helpers/getSearchFilterObject';
import { getActivityLogs } from '../../api/userManagement/logs';
import { getStartEnd } from '../../helpers/getStartEnd'
import ActivityLogsFilter from '../../App/components/SearchFilters/ActivityLogsFilter';

const ActivityLogs = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [activitiesArr, setActivitiesArr] = React.useState([]);
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();
        const requestJson = {
            "action": "get",
            "filter": "none",
            ...getActivityLogsFilter(props),
            ...getStartEnd(props)
        };
        console.log(JSON.stringify(requestJson))
        getActivityLogs(requestJson, controller.signal)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setActivitiesArr(res.json.logs);
                    setInstanceCount(res.json.count);
                    setLoading(false);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });
        return () => controller.abort()
    }, []);



    const ActivitiesList = activitiesArr.map((instance, index) => {
        return (
            <tr key={index}>
                <th scope="row">{instance.id}</th>
                <th
                    scope="row"
                    style={{ cursor: 'pointer' }}
                    onClick={() => props.history.push('/user/' + instance.user)}
                >
                    {instance.user_details.first_name}
                </th>

                <td>{instance.action[0].toUpperCase() + instance.action.slice(1)}d[{instance.object_str}]</td>
                <td>{instance.model}</td>
                <td>{(new Date(instance.date)).toString()}</td>


            </tr>
        )
    })

    return (
        <Aux>
            <Row>
                <Col>
                    <ActivityLogsFilter />
                    <Table striped responsive>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>User</th>
                                <th>Action</th>
                                <th>Model</th>
                                <th>Log Time</th>
                            </tr>
                        </thead>
                        <tbody>
                            {ActivitiesList || ''}
                        </tbody>
                    </Table>
                    {isEmpty(activitiesArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                    {loading ? <Spinner /> : null}
                    <Paging instanceCount={instanceCount} />

                </Col>
            </Row>
        </Aux>
    );
}

export default ActivityLogs;