import React from 'react';

import { Row, Col, Card, Table, Form, Collapse } from 'react-bootstrap';

import AsyncSelect from 'react-select/async';
import { withRouter } from 'react-router-dom';
import DatePicker from 'react-datepicker';
import isEmpty from '../../../validation/isEmpty';
import { getItemCatagories } from '../../../api/inventory/itemCategory';
import Jhilikka from '../Reusables/Jhilikka';
import { getCustomers } from '../../../api/sales/customer';
import { getAccounts, getAccountTypes } from '../../../api/accounting/account';

import { getVendors } from '../../../api/inventory/vendorApi';
import { getUsers } from '../../../api/userManagement/users';

const filterDataModel = {
    dateFrom: '',
    dateUpto: '',
    user: '',
    userName: '',
    objectId: '',
    objectName: '',
    action: '',
    model: '',
}

function AuthLogsFilter(props) {

    const [showFilters, setShowFilters] = React.useState(false);

    const [filterData, setFilterData] = React.useState({ ...filterDataModel });

    //extract the query params to fill the filter's form
    React.useEffect(() => {

        const queryString = props.location.search;
        const queryParams = new URLSearchParams(queryString) || '';
        let dateFrom = queryParams.get('dateFrom') ? new Date(queryParams.get('dateFrom')) : '';
        let dateUpto = queryParams.get('dateUpto') ? new Date(queryParams.get('dateUpto')) : '';
        let user = queryParams.get('user') || '';
        let userName = queryParams.get('userName') || '';
        let action = queryParams.get('action') || '';
        let objectId = queryParams.get('objectId') || '';
        let objectName = queryParams.get('objectName') || '';
        let model = queryParams.get('model') || '';

        setFilterData({
            ...filterData,
            dateFrom,
            dateUpto,
            user,
            userName,
            action,
            objectId,
            objectName,
            model,
        })
    }, [])

    const loadUserOptions = async (inputText) => {
        const requestJson = { action: 'get', filter: 'name', name: inputText, start: 0, end: 50 }
        return getUsers(requestJson)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    return res.json.users.map((instance, index) => ({ label: `${instance.first_name} ${instance.middle_name || ''} ${instance.last_name} (${instance.username})`, value: instance.id }));
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
    }





    const _selectedUserChange = (user) => {
        setFilterData({
            ...filterData,
            user: user ? user.value : '',
            userName: user ? user.label : '',
        })
    }

    const _dateFromChange = (selectedDate) => {
        setFilterData({ ...filterData, dateFrom: selectedDate })
    }

    const _dateUptoChange = (selectedDate) => {
        setFilterData({ ...filterData, dateUpto: selectedDate })
    }
    const _filterDataChange = (e) => {
        const { name, value, type } = e.target;
        setFilterData({
            ...filterData,
            [name]: (type === 'number' ? parseFloat(value) : value) || ''
        })
    }


    const _keypress = (e) => {
        if (e.key === 'Enter') {
            _search();
        }
    }


    const _search = () => {
        //append the query params
        let queryParams = new URLSearchParams();
        Object.keys(filterData).forEach(key => {
            if (filterData[key]) {
                queryParams.append(key, filterData[key])
            }
        });
        let queryString = queryParams.toString();

        props.history.replace({ search: queryString });
    }


    const _resetFilters = () => {
        setFilterData({ ...filterDataModel });
        props.history.replace();
    }

    //check if there are extra filters
    const NotificationDot = <div style={{
        background: "#a6c0ce",
        width: "12px",
        height: "12px",
        borderRadius: "50%",
        position: "relative",
        top: "-5px",
        left: "-7px",
    }}
        className="itemsSearchFilterNotificationDot"
    ></div>

    let showNotificationDot = false;
    for (let key in filterData) {
        if (key !== 'userName' && key !== 'user') {
            if (!isEmpty((filterData[key] || '').toString())) {
                showNotificationDot = true;
            }
        }
    }

    return (
        <>
            <Card.Header className="filterInputs" onKeyDown={_keypress}>
                <div className="filterHeader" >
                    <div style={{ display: 'flex', flex: 9 }}>

                        <Col style={{ paddingLeft: 0, paddingRight: 0 }}>
                            <AsyncSelect
                                style={{ width: '100%' }}
                                value={isEmpty(filterData.user) ? null : { label: filterData.userName + `${filterData.username}`, value: filterData.user }}
                                onChange={(user) => _selectedUserChange(user)}
                                loadOptions={loadUserOptions}
                                isClearable={true}
                                className="mainSearchBox"
                                placeholder='Search by User'
                            />
                        </Col>

                        <button
                            className="btn btn-secondary btn-sm"
                            onClick={_search}
                            style={{ padding: '4px 8px', margin: 0, marginLeft: '2px' }}
                        > <i className="feather icon-search text-c-white f-8 mr-1" />Search</button>
                    </div>

                    <button
                        className="btn btn-secondary btn-sm showFilterButton"
                        onClick={() => setShowFilters(!showFilters)}

                    >
                        <div className="filterWrapper">
                            <i className="feather icon-sliders text-c-white f-8" />
                        </div>
                        <span>{showFilters ? "Hide" : "Show"} Filters
                        </span>

                    </button>
                    {showNotificationDot ? NotificationDot : null}

                </div>

                <Collapse in={showFilters}>
                    <div id="basic-collapse" >
                        <Card.Body className="filterBox pr-3 pl-3 pb-0">
                            <Row>
                                <Col md={3}>
                                    <Form.Label>Action</Form.Label>
                                    <Form.Control name='action' value={filterData.action} onChange={_filterDataChange} as="select">

                                        <option value='' >Any</option>
                                        <option value={'delete'}>Delete</option>
                                        <option value={'create'}>Create</option>
                                        <option value={'update'}>Update</option>
                                        <option value={'soft_delete'}>Soft Delete</option>
                                    </Form.Control>
                                </Col>
                                <Col md={3}>
                                    <Form.Label>Interval</Form.Label>
                                    <div style={{ display: 'flex' }}>
                                        <DatePicker
                                            onChange={_dateFromChange}
                                            placeholderText="Date From"
                                            name="dateFrom"
                                            selected={filterData.dateFrom}
                                        />
                                        <div style={{ width: '8px' }}></div>
                                        <DatePicker
                                            onChange={_dateUptoChange}
                                            placeholderText="Date Upto"
                                            name="dateUpto"
                                            selected={filterData.dateUpto}
                                        />
                                    </div>
                                </Col>
                                <Col md={3}>
                                    <Form.Label>Object ID</Form.Label>
                                    <Form.Control
                                        name="objectId"
                                        value={filterData.objectId}
                                        onChange={_filterDataChange}
                                        type="text"
                                        placeholder="Object ID"
                                        size="sm"
                                    />
                                </Col>

                                <Col md={3}>
                                    <Form.Label>Object Name</Form.Label>
                                    <Form.Control
                                        name="objectName"
                                        value={filterData.objectName}
                                        onChange={_filterDataChange}
                                        type="text"
                                        placeholder="Object Name"
                                        size="sm"
                                    />
                                </Col>
                            </Row>
                            <br />

                            <Row>
                                <Col md={3}>
                                    <Form.Label>Model</Form.Label>
                                    <Form.Control
                                        name="model"
                                        value={filterData.model}
                                        onChange={_filterDataChange}
                                        type="text"
                                        placeholder="Model (eg. sales/invoice)"
                                        size="sm"
                                    />
                                </Col>
                            </Row>
                            <br />

                            <Row style={{ display: 'flex', justifyContent: 'flex-end' }} >
                                <button
                                    className="btn btn-outline-secondary btn-sm mr-2"
                                    onClick={_resetFilters}
                                    style={{ padding: '4px 8px', margin: 0, marginLeft: '2px' }}
                                >Reset Filters</button>
                                <button
                                    className="btn btn-secondary btn-sm mr-4"
                                    onClick={_search}
                                    style={{ padding: '4px 8px', margin: 0, marginLeft: '2px' }}
                                >Apply Filters</button>

                            </Row>

                        </Card.Body>

                    </div>
                </Collapse>

            </Card.Header>
        </>
    )
}

export default withRouter(AuthLogsFilter);